<template>
  <div class="container" style="min-height: 597px;">
    <div class="block-list address-list section section-first js-no-webview-block">
      <a @click="toEdit()"
        class="block-item js-address-item address-item" :class="{'address-item-default':address.default}"
        href="javascript:;" v-for="address in list" :key="address.id"
      >
        <div class="address-title">{{address.name}} {{address.tel}}</div>
        <p>{{address.provinceName}}{{address.cityName}}{{address.districtName}}</p>
        <a class="address-edit" >修改</a>
      </a>
      
    </div>
    <div class="block stick-bottom-row center">
      <router-link
        class="btn btn-blue js-no-webview-block js-add-address-btn" to="/address/form"
      >新增地址
      </router-link>
    </div>
  </div>
</template>

<script>
import Address from 'js/addressService.js'

export default {
  data(){
    return {
      list: null
    }
  },
  methods:{
    toEdit(){
        this.$router.push({path: '/address/form'})
    }
  },
  created(){
    Address.getlist().then((res)=>{
       res.data.addresslist[0].default = true
       this.list = res.data.addresslist
    })
  }
}
</script>





